nav{
    min-height: 10vh;
    display:flex;
    justify-content: space-around;
    align-items: center;
    button{
        padding: 1rem ;
        background: transparent;
        border: 2px solid black;
        cursor:pointer;
        transition: all 0.3s ease;
        &:hover{
            background: rgb(65,65,65);
            color:white;
        }
    }
}
@media screen and ( max-width: 768px){
nav{
    button{
        z-index:10;
    }
}
}