* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h2, h3 {
  color: black;
  font-weight: bold;
}

body {
  font-family: "Edu NSW ACT Foundation", cursive;
  font-family: "Nunito", sans-serif;
}

h3, h4 {
  font-weight: 400;
  color: rgb(12, 12, 12);
}

.App {
  padding-bottom: 0px;
  min-height: 100vh;
  transition: all 0.5s ease;
  overflow: hidden;
}

.library-active {
  margin-left: 20%;
}
.player {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}

.time-control {
  width: 50%;
  display: flex;
}
.time-control input {
  width: 100%;
  padding: 1rem 0rem;
  cursor: pointer;
}
.time-control p {
  padding: 1rem;
}

.play-control {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 50%;
}
.play-control svg {
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .time-control {
    width: 80%;
  }
}
.song-container {
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.song-container img {
  width: 20%;
  border-radius: 50%;
}
.song-container h2 {
  padding: 3rem 1rem 1rem 1rem;
}
.song-container h3 {
  padding: 1rem;
}

@media screen and (max-width: 768px) {
  .song-container img {
    width: 60%;
  }
}
nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
nav button {
  padding: 1rem;
  background: transparent;
  border: 2px solid black;
  cursor: pointer;
  transition: all 0.3s ease;
}
nav button:hover {
  background: rgb(65, 65, 65);
  color: white;
}

@media screen and (max-width: 768px) {
  nav button {
    z-index: 10;
  }
}
.library {
  position: fixed;
  top: 0px;
  left: 0;
  width: 20rem;
  min-height: 100vh;
  background-color: white;
  box-shadow: 2px 2px 50px gray;
  overflow: scroll;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: all 0.5s ease;
  opacity: 0;
}
.library h2 {
  padding: 2rem;
}

.library-song {
  display: flex;
  align-items: center;
  transition: all 0.6s ease;
  padding: 1rem 2rem 1rem 2rem;
  cursor: pointer;
}
.library-song img {
  width: 30%;
}
.library-song:hover {
  background: #ffcad4;
}

.song-description {
  padding-left: 1rem;
  transition: all 0.5s ease;
}
.song-description h3 {
  font-size: 1rem;
}
.song-description h4 {
  font-size: 0.7rem;
}

* {
  scrollbar-width: thin;
}

*::-webkit-scrollbar-track {
  background: transparent;
}

*::-webkit-scrollbar {
  width: 5px;
}

.selected {
  background-color: #ffe5d9;
}

.active-library {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
  opacity: 1;
}

@media screen and (max-width: 768px) {
  .library {
    width: 100%;
  }
}
